/* Clip-path transition */
.clip-leave {
    opacity: 1;
}

.clip-leave-active {
    transition: opacity .1s linear;
}

.clip-leave-to {
    opacity: 0; 
}

.clip-enter {
    clip-path: circle(0%);
}

.clip-enter-active {
    transition: all .5s linear ;
    position: absolute;
    top: 0;
    z-index: 2;
}

.clip-enter-to {    
    clip-path: circle(75%);
}