
// @import '~reset-css/reset.css';
@import 'home.scss';
@import 'fade.scss';
@import 'clip.scss';
@import 'with-cover.scss';

:root {
  --easing: cubic-bezier(0.5, 0.7, 0.4, 1);
}

body {
  font-weight: 300;
  margin: 0%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 900;
}

header {
  --base-color: #fff;


  color: var(--base-color);

  padding: 0 14vw;

  h1 {
    font-size: 5vw;
    margin-bottom: 2.5vh;
    line-height: 1.4;
  }



  a {
    font-size: 2.22vw;
    padding: 24px 2vw;
    display: inline-block;
    text-align: center;
    color: var(--base-color);
    border: 3px solid  var(--base-color);
    border-radius: 10px;
    text-decoration: none;
  }

  &.is-home {
    background-color: #5c85ee;
  }

  &.is-fade {
    --base-color: #000;

    background-color: #cc98c6;
  }

  &.is-clip {
    background-color: #161636;
  }

  &.is-cover {
    background-color: #e66f7f;
  }
}

.content {
  transition: all 0.3s ease-out;
  opacity: 1;
  transform: translateY(0);
  height: 100vh;
}

.transition {
  position: absolute;
  z-index: 99;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 50%;
  height: 50vh;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  pointer-events: none;
}

.transition li {
  width: 20%;
  -webkit-transform: scaleY(.5);
          transform: scaleY(.5);
  background: #fffffe;
}
