/* Page load animation */
.home-once {
    opacity: 0;
}

.home-once-active {
    transition: opacity 3s linear ;
}

.home-once-to {
    opacity: 1;
}

.home-leave {
    transform: translateX(0%);
}

.home-leave-active {
    transition: all 0.7s var(--easing) ;
}

.home-leave-to {
    transform: translateX(100%);
}

.home-enter {
    transform: translateX(-100%);
}

.home-enter-active {
    transition: all 0.7s var(--easing) ;
}

.home-enter-to {
    transform: translateX(0%);
    position: absolute;
    top: 0%;
    left: 0%;
}
